import React from 'react'
import { connect } from 'react-redux'
import { login } from '../actions'
import { Well } from 'react-bootstrap';
let Login = ({ dispatch, hidden, loading }) =>
    (<div style={{
        display: hidden ? 'none' : ''
    }}> <div>
            {
                loading ?
                    (
                        <div className="col-md-12 text-center"><Well><img src={'images/loading.gif'} />
                            <p>...</p></Well></div>
                    )
                    : (<div><div className="banner">
                        <img src={'images/banner.png'} />
                    </div>
                        <div className="login-box">
                            <form action="" method="get">
                                <input className="input-username" ref={userName => this.userName = userName} type="text" placeholder="" />
                                <input className="input-username input-password" ref={password => this.password = password} type="password" placeholder="" />
                                <button className="btn-login" name="submit" type="button" onClick={() => {
                                    if (this.userName.value == '') {
                                        alert('')
                                        return null
                                    }
                                    if (this.password.value == '') {
                                        alert('')
                                        return null
                                    }
                                    dispatch(login({ userName: this.userName.value, password: this.password.value }))
                                }} > </button>
                            </form>
                        </div>
                        <div className="footer"></div></div>)
            }
        </div>


    </div>)





const mapStateToProps = (state) => {
    let hidden = state.showPage !== 0
    let loading = state.loading
    return { hidden, loading }
}

/*const mapDispatchToProps = {
    login: login
}
*/

Login = connect(
    mapStateToProps/*, mapDispatchToProps*/
)(Login)


//TopNav = connect()(TopNav)
export default Login



